// card styles
.card {
  display: block;
  width: 100%;
  height: 300px;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  .card-content {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    p {
      margin: 0;
    }
  }
  .card-image {
    display: block;
    width: 100%;
    height: 100%;
    img {
      @include objectfit-cover;
      display: block;
    }
    &::after {
      content: '';
      width: calc(100% + 1px);
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(bottom, rgba(0,0,0,0.8), rgba(0,0,0,0))
    }
  }
}