/* =====================
Layout
===================== */
%vh {
    @include visuallyhidden();
}

%clearfix { @include clearfix(); }

%container {
    @extend %clearfix;
    margin: auto;
    max-width: $bpLgDesktop;
    position: relative;
    width: 100%;
    padding: 0 20px;
}

%container-lg {
  @extend %container;
  max-width: $bpXLgDesktop;
}

%list-unstyled {
	@include normalise-list;
}

.container {
	@extend %container;
}

.container-lg {
  @extend %container-lg;
}

.vh, .sr-only {
    @extend %vh;
}

.img-responsive {
	display: block;
	height: auto;
	margin: auto;
	max-width: 100%;
	width: 100%;
}

.table-responsive {
	width: 100%;
	overflow-x: auto;
}

.hide {
	display: none;
}

.row {
	@extend %clearfix;
	margin: 0 -10px;
}

.reverse-col {
	[class^="col-"], [class*=" col-"] {
		float: right;
	}
}



[class^="col-"], [class*=" col-"] {
	float: left;
	width: 100%;
	position: relative;
	padding: 15px 15px;
}

.col-float-right-mobile {
	@include mobile {
		float: right;
	}
}


[class^="col-offset-"], [class*=" col-offset-"] {
	margin: auto;
}

@for $i from 1 through 12 {
	.col-xs-#{$i} {
		width: (100% / 12 * $i);
	}

	.col-offset-xs-#{$i} {
		margin-left: (100% / 12 * $i);
	}
}

@media only screen and (max-width: $bpTablet - 1) {
	.desktop-only {
		display: none !important;
	}
	.no-mobile {
		display: none;
	}
	.mobile-only {
		display: block !important;
	}
}

@media only screen and (min-width: $bpTablet) {
	@for $i from 1 through 12 {
		.col-sm-#{$i} {
			width: (100% / 12 * $i);
		}

		.col-offset-sm-#{$i} {
			margin-left: (100% / 12 * $i);
		}
	}

	.desktop-only {
		display: none !important;
	}

	.mobile-only {
		display: block !important;
	}
}

@media only screen and (min-width: $bpDesktop) {
	@for $i from 1 through 12 {
		.col-md-#{$i} {
			width: (100% / 12 * $i);
		}

		.col-offset-md-#{$i} {
			margin-left: (100% / 12 * $i);
		}
  }

	.desktop-only {
		display: block !important;
	}
  .no-desktop {
    display: none;
	}
	.mobile-only {
		display: none !important;
	}
}

@media only screen and (min-width: $bpLgDesktop) {
	@for $i from 1 through 12 {
		.col-lg-#{$i} {
			width: (100% / 12 * $i);
		}

		.col-offset-lg-#{$i} {
			margin-left: (100% / 12 * $i);
		}
	}
}


.col-flex {
	display: flex;
	justify-content: space-around;
	height: 100%;
	width: 100%;
	padding: 0;
	&-row {
		flex-direction: row;
	}
	&-column {
		flex-direction: column;
	}
	&-sm-row {
		@include tablet {
			flex-direction: row;
		}
	}
	&-sm-column {
		@include tablet {
			flex-direction: column;
		}
	}
	&-xs-row {
		@include mobile {
			flex-direction: row;
		}
	}
	&-xs-column {
		@include mobile {
			flex-direction: column;
		}
	}
}

.container-v-center {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	height: 100%;
}

.general-content {
  background-color: white;
  padding: 40px 0px;
}

.content-section {
	padding-bottom: 60px;
}


.no-padding {
	padding: 0 !important;
}

.padding-left-m {
	padding-left: 30px;
}

.padding-right-m {
	padding-right: 30px;
}

.text-inline {
	display: inline;
}

.col-md-float-right {
	@include desktop {
		float: right;
	}
}

.col-sm-float-right {
	@include tablet {
		float :right;
	}
}

.col-xs-float-right {
	@include mobile {
		float: right;
	}
}