// banner styles
.banner {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: black;
  .banner-slide {
    width: 100vw;
    height: 100vh;
  }
  .banner-image {
    display: block;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      display: block;
      @include objectfit-cover; 
    }
    &:before {
      content:'';
      display: block;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 1;
      position: absolute; 
      background: radial-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.8))
    }
  }
  .banner-content {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    h1 {
      font-size: calc-em(60px);
      padding: 10px 10px;
      margin: 0;
    }
    text-align: center;

  }
}