  font-style: normal;
  font-weight: normal;
  src: url("/fonts/Gloss_And_Bloom.ttf");
  /* IE9 Compat Modes */
}

.btn {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 0.8125em;
  letter-spacing: 0.3em;
  background-color: #d22124;
  padding: 10px 20px;
  display: inline-block;
  font-weight: bold;
  box-shadow: 0px 7px 40px rgba(0, 0, 0, 0.2);
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.btn:hover {
  background-color: white;
  color: #d22124;
}

.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  z-index: 999;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: 100%;
  background-color: #d22124;
}

@media (min-width: 1024px) {
  .header {
    height: 100%;
    width: auto;
    background-color: rgba(0, 0, 0, 0.7);
  }
}

.header nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding: 10px 20px;
  align-items: center;
}

@media (min-width: 1024px) {
  .header nav {
    padding: 0;
    flex-direction: column;
    height: 100%;
    justify-content: flex-start;
  }
}

.header .site-logo img {
  width: 150px;
  height: auto;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .header .site-logo img {
    width: 100px;
  }
}

@media (max-width: 767px) {
  .header .site-logo img {
    width: 80px;
  }
}

.header .menu-icon {
  cursor: pointer;
  display: block;
}

@media (min-width: 1024px) {
  .header .menu-icon {
    display: none;
  }
}

.header .menu-icon span {
  display: block;
  width: 30px;
  height: 3px;
  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  background-color: white;
}

.header .menu-icon span.top {
  transform: translateY(-5px);
}

.header .menu-icon span.bottom {
  transform: translateY(5px);
}

.header .menu-icon.active span.middle {
  opacity: 0;
}

.header .menu-icon.active span.top {
  transform: translateY(3px) rotate(45deg);
}

.header .menu-icon.active span.bottom {
  transform: translateY(-3px) rotate(-45deg);
}

.header ul {
  display: block;
  padding: 20px 20px;
  list-style-type: none;
  display: none;
  width: 200px;
  z-index: 998;
  background-color: black;
  position: fixed;
  top: 65px;
  right: 0;
  height: auto;
  flex-direction: column;
  justify-content: center;
}

@media (min-width: 1024px) {
  .header ul {
    position: relative;
    height: auto;
    width: auto;
    display: flex !important;
    background-color: transparent;
    flex-direction: column;
    justify-content: center;
  }
}

.header ul li {
  display: block;
  padding: 15px 0px;
  text-align: center;
  cursor: pointer;
}

.header ul li.active span {
  color: #d22124;
}

.header ul li span {
  font-weight: bold;
  color: white;
  font-size: 0.875em;
  font-weight: bold;
  letter-spacing: 1.2px;
}

.header ul li span:hover {
  color: #d22124;
}

.banner {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  background-color: black;
}

.banner .banner-slide {
  width: 100vw;
  height: 100vh;
}

.banner .banner-image {
  display: block;
  width: 100%;
  height: 100%;
}

.banner .banner-image img {
  width: 100%;
  height: 100%;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: "object-fit: cover;";
}

.banner .banner-image:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  position: absolute;
  background: radial-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8));
}

.banner .banner-content {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.banner .banner-content h1 {
  font-size: 3.75em;
  padding: 10px 10px;
  margin: 0;
}

.card {
  display: block;
  width: 100%;
  height: 300px;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}

.card .card-content {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  display: flex;
  padding: 20px;
  justify-content: space-between;
}

.card .card-content p {
  margin: 0;
}

.card .card-image {
  display: block;
  width: 100%;
  height: 100%;
}

.card .card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: "object-fit: cover;";
  display: block;
}

.card .card-image::after {
  content: '';
  width: calc(100% + 1px);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
}

.landing-section {
  opacity: 0;
}

.landing-section .banner-slide .banner-content {
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) 0.3s;
}

.landing-section.visible {
  opacity: 1;
}

.landing-section.visible .banner-slide .banner-content {
  opacity: 1;
  transform: translateY(0px);
}

.content-section {
  background-color: #000;
  display: block;
  height: 100%;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
}

@media (min-width: 1024px) {
  .content-section {
    height: 100vh;
  }
}

.content-section .section-cover {
  background-color: #d22124;
  width: 100%;
  height: 35vh;
  position: relative;
  text-align: center;
  padding-top: 15vh;
}

@media (min-width: 1024px) {
  .content-section .section-cover {
    padding-top: 0;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    width: 40%;
  }
}

.content-section .section-cover img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: "object-fit: cover;";
  z-index: 0;
  display: block;
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
}

.content-section .section-cover h1 {
  position: relative;
  z-index: 2;
  font-size: 2.0625em;
  transform: translateX(0);
  color: white;
  position: relative;
  padding-top: 20px;
  padding-bottom: 0;
  opacity: 0;
  transform: translateX(-100%);
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
}

@media (min-width: 1024px) {
  .content-section .section-cover h1 {
    font-size: 4.125em;
    padding-top: 100px;
    transform: translateX(60%);
  }
}

.content-section .section-cover h1::after {
  content: '___';
  font-family: 'gloss';
  position: absolute;
  left: 50%;
  bottom: -20px;
  font-size: 44px;
  color: #d22124;
}

@media (min-width: 1024px) {
  .content-section .section-cover h1::after {
    font-size: 100px;
    left: 5%;
    bottom: -40px;
  }
}

.content-section .content-wrapper {
  padding: 30px 30px;
  font-size: 14px;
  letter-spacing: 1px;
  height: 100%;
  overflow: auto;
  opacity: 0;
  transform: translateY(200px);
  transition: all cubic-bezier(0.165, 0.84, 0.44, 1) 0.5s;
}

@media (min-width: 1024px) {
  .content-section .content-wrapper {
    height: 100vh;
    font-size: 20px;
    padding-left: calc(40% + 60px);
    padding-right: 60px;
    padding-top: 350px;
    padding-bottom: 60px;
  }
}

@media (max-width: 767px) {
  .content-section .content-wrapper {
    height: auto;
    font-size: 14px;
    padding: 30px 30px;
  }
}

.content-section .content-wrapper h1 {
  font-size: 18px;
}

@media (min-width: 1024px) {
  .content-section .content-wrapper h1 {
    font-size: 34px;
  }
}

.content-section.visible .section-cover img {
  opacity: 1;
}

.content-section.visible .section-cover h1 {
  opacity: 1;
  transform: translateX(0);
}

@media (min-width: 1024px) {
  .content-section.visible .section-cover h1 {
    transform: translateX(60%);
  }
}

.content-section.visible .content-wrapper {
  opacity: 1;
  transform: translateY(0px);
}

.content-section.visible .content-wrapper .contact-item {
  padding-bottom: 30px;
}

.content-section.visible .content-wrapper .contact-item h2 {
  font-family: 'gloss', sans-serif;
  font-size: 24px;
}

.content-section.visible .content-wrapper .contact-item p {
  font-size: 16px;
}

.dish-category {
  padding-bottom: 50px;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

body {
  font-family: "Oxygen", Arial, sans-serif;
  padding: 0;
  margin: 0;
  background-color: #000;
  line-height: 1.5;
  color: white;
}

h1 {
  font-family: 'gloss', sans-serif;
  font-size: 2.75em;
}

.red {
  color: #d22124;
}

.page {
  display: none;
}

.page.visible {
  display: block;
}
