/* Breakpoints */
$bpMobile: 320px;
$bpTablet: 768px;
$bpDesktop: 1024px;
$bpLgDesktop: 1280px;
$bpXLgDesktop: 1366px;

/* Font Weight */
$base-fontsize: 16px;
$base-lineheight: 1.2;
$thin: 100;
$light: 300;
$normal: 400;
$medium: 500;
$semibold: 600;
$bold: 700;
$ultrabold: 900;

/* Colors */
$red: rgba(210, 33, 36, 1);
$transparentRed: rgba(210, 33, 36, 0.8);
$darkred: rgb(182, 23, 60);

$darkGrey: #1e1e1e;
$darkGrey2: #707070;
$darkGrey3: #525252;

$grey: #e8e9ed;
$grey2: #999999;
$grey3: #f9f9f9;
$grey4: #979797;

$facebook: #3B5998;
$twitter: #1da1f2;
$linkedin: #0077B5;
$youtube: #ff0000;

$green:  #83D5C1;
$blue : #414157;

$borderGrey: $grey;
$backgroundGrey: $grey3;

$headerColor: $darkGrey;
$bodyTextColor: $darkGrey2;

$banner-gradient: linear-gradient(90deg, rgba(255, 255, 255, 0.85) 0%, rgba(255, 255, 255, 0.85) 49.01%, rgba(255, 255, 255, 0) 63.44%);

/* Font style */
$headerFont: 'Bitter', Times News Roman, serif;
$bodyFont: 'Open Sans', Arial, sans-serif;

/* Expo */
$easeInOutExpo: cubic-bezier(0.75, 0, 0, 0.75);
$easeInOutBack: cubic-bezier(0.68, -0.55, 0.265, 1.55);

/* Shadow */
$cardShadow: 5px 5px 10px 0 transparentize(#000, .75);
$buttonShadow: 2px 2px 6px 0 transparentize(#000, .75);
