// landing-section styles
.landing-section {
  opacity: 0;
  .banner-slide {
    .banner-content {
      opacity: 0;
      transform: translateY(100px);
      transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) 0.3s;
    }
  }
  &.visible {
    opacity: 1;
    .banner-slide {
      .banner-content {
        opacity: 1;
        transform: translateY(0px);
      }
    }
  }
}