// header styles
.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.7);
  box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
  z-index: 999;
  justify-content: flex-start;
  align-items: center;
  height: auto;
  width: 100%;
  background-color: $red;
  @include desktop {
    height: 100%;
    width: auto;
    background-color: rgba(0,0,0,0.7);
  }
  nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 10px 20px;
    align-items: center;
    @include desktop {
      padding: 0;
      flex-direction: column;
      height: 100%;
      justify-content: flex-start;
    }
  }
  .site-logo {
    img {
      width: 150px;
      height: auto;
      @include tablet {
        width: 100px;
      }
      @include mobile {
        width: 80px;
      }
    }
  }

  .menu-icon {
    cursor: pointer;
    display: block;
    @include desktop{
      display: none;
    }
    span {
      display: block;
      width: 30px;
      height: 3px;
      transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
      background-color: white;
      &.top {
        transform: translateY(-5px);
      }
      &.bottom {
        transform: translateY(5px);
      }
    }
    &.active {

      span {
        &.middle {
          opacity: 0;
        }
        &.top {
          transform: translateY(3px) rotate(45deg);
        }
        &.bottom {
          transform: translateY(-3px) rotate(-45deg);
        }
      }
    }
  }
  ul {
    display: block;
    padding: 20px 20px;
    list-style-type: none;
    display: none;
    width: 200px; 
    z-index: 998;
    background-color: black;
    position: fixed;
    top: 65px;
    right: 0;
    height: auto;
    flex-direction: column;
    justify-content: center;
    @include desktop {
      position: relative;
      height: auto;
      width: auto; 
      display: flex !important;
      background-color: transparent;
      flex-direction: column;
      justify-content: center;
    }
    li {
      display: block;
      padding: 15px 0px;
      text-align: center;
      cursor: pointer;
      &.active {
        span {
          color: $red;
        }
      }
      span {
        font-weight: bold;
        color: white;
        font-size: calc-em(14px);
        font-weight: bold;
        letter-spacing: 1.2px;
        
        &:hover {
          color: $red;
        }
      }
    }
  }
  
}