// content-section styles
.content-section {
  background-color: #000;
  display: block;
  height: 100%;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
  @include desktop {
    height: 100vh;
  }
  .section-cover {
    background-color: $red;
    width: 100%;
    height: 35vh;
    position: relative;
    text-align: center;
    padding-top: 15vh;
    @include desktop {
      padding-top: 0;
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
      width: 40%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @include objectfit-cover;
      z-index: 0;
      display: block;  
      opacity: 0;
      transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
    }
    h1 {
      position: relative;
      z-index: 2;
      font-size: calc-em(33px);
      transform: translateX(0);
      color: white;
      position: relative;
      padding-top: 20px;
      padding-bottom: 0;
      opacity: 0;
      transform: translateX(-100%);
      transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 0.2s;
      @include desktop {
        font-size: calc-em(66px);
        padding-top: 100px;
        transform: translateX(60%);
      }
      &::after {
        content: '___';
        font-family: 'gloss';
        position: absolute;
        left: 50%;
        bottom: -20px;
        font-size: 44px;
        color: $red;
        @include desktop {
          font-size: 100px;
          left:5%;
          bottom: -40px;
        }
      }

    }
  } 
  .content-wrapper {
    padding: 30px 30px;
    font-size: 14px;
    letter-spacing: 1px;
    height: 100%;
    overflow: auto;
    opacity: 0;
    transform: translateY(200px);
    transition: all cubic-bezier(0.165, 0.84, 0.44, 1) 0.5s;
    @include desktop {
      height: 100vh;
      font-size: 20px;
      padding-left: calc(40% + 60px);
      padding-right: 60px;
      padding-top: 350px;
      padding-bottom: 60px;
    }
    @include mobile {
      height: auto;
      font-size: 14px;
      padding: 30px 30px;
    }
    h1 {
      font-size: 18px;
      @include desktop {
        font-size: 34px;
      }
    }
  }
  &.visible {
    .section-cover {
      img {
        opacity: 1;
      }
      h1 {
        opacity: 1;
        transform: translateX(0);
        @include desktop {
          transform: translateX(60%);
        }
      }
      
    }
    .content-wrapper {
      opacity: 1;
      transform: translateY(0px);
      .contact-item {
        h2 {
          font-family: 'gloss', sans-serif;
          font-size: 24px;
        }
        p {
          font-size: 16px;
        }
        padding-bottom: 30px;
      }
      
    }
  }
}

.dish-category {
  padding-bottom: 50px;
}
