// Normalize Styles
@import 'node_modules/normalize.css/normalize';
@import 'node_modules/slick-carousel/slick/slick.scss';

// Import Modules

@import '_mixins';
@import '_variables';
@import '_helpers';
@import '_fonts';

@import '../_modules/atoms/button/button';
@import '../_modules/organisms/header/header';
@import '../_modules/molecules/banner/banner';
@import '../_modules/molecules/card/card';
@import '../_modules/organisms/landing-section/landing-section';
@import '../_modules/organisms/content-section/content-section';
// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

// Styling on warning for users on IE7 or below
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

body {
  font-family: "Oxygen", Arial, sans-serif;
  padding: 0;
  margin: 0;
  background-color: #000;
  line-height: 1.5;
  color: white;
}

h1 {
  font-family: 'gloss', sans-serif;
  font-size: calc-em(44px);
}



.red {
  color: $red;
}

.page {
  display: none;
  &.visible {
    display: block;
  }
}